.personal_area {
  max-width: 1140px;
  margin: 0 auto;
  height: 100%;
  padding-top: 60px;
  display: grid;
  grid-template-columns: 22% 83%;
  padding-top: 30px;
  @media (max-width: 1250px) {
      padding-top: 10px;
      grid-template-columns: 100%;
  }
}

.border {
  width: 100%;
  height: 1px;
  background: #4f4b5c;
}
